
import { baseSlice } from './baseSlice';
export function dropRight(array: any[], n?: number) {
    const length = array == null ? 0 : array.length; // 
    if (!length) {// 
        return [];
    }
    n = n === undefined ? 1 : n;
    n = length - n;
    return baseSlice(array, 0, n < 0 ? 0 : n); // baseSlicen
}
