
import { baseSlice } from './baseSlice';
export function drop(array: any[], n?: number) {
    const length = array == null ? 0 : array.length; // 
    if (!length) {// 
        return [];
    }
    n = n === undefined ? 1 : n;
    return baseSlice(array, n < 0 ? 0 : n, length); // baseSlicen
}
